import { createWebHistory, createRouter } from "vue-router";
import LandingPage from "./components/LandingPage.vue";
import DownloadDemoPage from "./components/DownloadDemoPage.vue";
import DemoFormPage from "./components/DemoFormPage.vue";
import PageNotFound from "./components/PageNotFound.vue"
import EditPage from "./components/EditPage.vue"
import DownloadPageMobile from "./components/downloadDemoPageMobile.vue"
import ProfilePage from "./components/ProfilePage.vue"
const routes = [
  {
    path: "/",
    component: LandingPage,
    name: "home",
  },

  {
    path: "/demo-form/:url",
    name: "demoForm",
    component: DemoFormPage,
  },

  {
    path: "/profile-page/:email",
    name: 'profilePage',
    component: ProfilePage,
  },

  {
    path: "/download-demo/:token/:os",
    name: "downloadDemo",
    component: DownloadDemoPage,
  },

  {
    path: "/edit/:token/:status/:email",
    name: "edit",
    component: EditPage,
  },

  {
    path: "/downloadPageMobile/:token/:os",
    name: "downloadPageMobile",
    component: DownloadPageMobile,
  },

  { path: "/:notFound", component: PageNotFound },
  {path: "/404", component: PageNotFound, name: "notFound" }
];
const router = createRouter({
  history: createWebHistory(),
  routes: routes,
});

export default router;
