<template>
  <div style="position: relative; overflow: hidden">
    <div
      class="fixed z-[0] inset-0 w-screen bg-base-100 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"
    ></div>
    <div
      class="navbar bg-primary absolute top-0 w-full z-[1] text-primary-content"
      v-if="!loading"
    >
      <div class="flex-1">
        <router-link to="/" class="btn btn-ghost normal-case text-xl"
          >web to app</router-link
        >
      </div>
      <div class="flex-none">
        <button onclick="my_modal_3.showModal()" class="btn">login</button>

        <dialog id="my_modal_3" class="modal">
          <div class="modal-box">
            <form method="dialog">
              <button
                class="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"
              >
                ✕
              </button>
            </form>
            <h3 class="font-bold text-lg">login</h3>
            <div class="pt-3 pb-3">
              <label for="email">Email</label>
              <input
                type="text"
                id="email"
                v-model="email"
                class="input input-bordered w-full"
                placeholder="youremail@example.com"
              />
            </div>
            <div class="w-full flex justify-center">
              <button class="btn btn-primary" @click="login">
                login
              </button>
            </div>
          </div>
        </dialog>
      </div>
    </div>

    <div class="hero min-h-screen bg-base-200">
      <div class="hero-content text-center flex flex-col m-0 p-0 mt-20">
        <div class="max-w-xl">
          <h1 class="text-6xl font-bold" v-show="showButton">
            Turn your website into a
            <span class="font-extrabold text-primary"> mobile app</span>
          </h1>
          <div class="join w-full p-6" v-show="showButton">
            <input
              class="input input-bordered join-item w-full"
              v-model="url"
              type="url"
              placeholder="website URL"
            />
            <button @click="sendUrl" class="btn join-item">start now</button>
          </div>
          <div class="w-full flex justify-center text-error my-0">
            <p>{{ errorMessage }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  data() {
    return {
      url: "",
      errorMessage: "",
      showButton: true,
      email: "",
    };
  },
  methods: {

    login() {
      this.$router.push({ name: "profilePage", params: { email: this.email } });
    },
    async sendUrl() {
      if (!this.isValidUrl(this.url)) {
        // Handle invalid URL
        this.errorMessage = "Invalid URL";
        console.log("Invalid URL");
        return;
      }
      if (!this.url) {
        return;
      }
      this.$router.push({ name: "demoForm", params: { url: this.url } });
    },
    isValidUrl(url) {
      const pattern = new RegExp(
        "^(https?:\\/\\/)" +
          // protocol
          "((([a-zA-Z\\d]([a-zA-Z\\d-]{0,61}[a-zA-Z\\d])?)\\.)+[a-zA-Z]{2,6}" +
          // domain name
          "|(\\d{1,3}\\.){3}\\d{1,3}" +
          // OR ip (v4) address
          "|localhost)" +
          // OR localhost
          "(\\:\\d+)?(\\/[-a-zA-Z\\d%_.~+]*)*" +
          // port and path
          "(\\?[;&a-zA-Z\\d%_.~+=-]*)?" +
          // query string
          "(\\#[-a-zA-Z\\d_]*)?$"
        // fragment locator
      );
      return !!pattern.test(url);
    },
  },
};
</script>
