<script>
import axios from "axios";

export default {
  data() {
    return {
      email: this.$route.params.email,
      info: null,
      loading: true,
      apps: [],
      watingForDownload: false,
    };
  },
  async created() {
    
    try {
      const response = await axios.post(
        "http://34.125.138.233/get-all-apps-android",
        {
          email: this.email,
        }
      );
      console.log(response.data.rows);
      this.apps = response.data.rows;
      this.loading = false;
    } catch (error) {
      console.error("Error fetching data:", error);
      if (error.response.status === 404) {
        this.loading = false;
        this.$router.push({ name: "notFound" });
      }
    }
  },

  methods: {
    editButton(token) {
      if (!this.loadingData) {
        this.$router.push({
          name: "edit",
          params: {
            token: token,
            status: "final",
            email: this.email,
          },
        });
      }
    },
    async downloadApk(token) {
      this.watingForDownload = true;
      if (!this.loadingData) {
        try {
          const response = await axios.get(
            `http://34.125.138.233/download-final-apk-android?token=${token}.0`,
            {
              responseType: "blob", // Set the response type to 'blob'
            }
          );
          this.watingForDownload = false;
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "app.apk"); // Set the desired file name
          document.body.appendChild(link);
          link.click();
        } catch (error) {
          this.watingForDownload = false;
          console.error("Error downloading apk", error);
          // Handle error as needed
        }
      }
    },

    async downloadBundle(token) {
      this.watingForDownload = true;
      if (!this.loadingData) {
        try {
          const response = await axios.get(
            `http://34.125.138.233/download-bundle-android?token=${token}.0`,
            {
              responseType: "blob", // Set the response type to 'blob'
            }
          );
          this.watingForDownload = false;
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "android-bundle.apk"); // Set the desired file name
          document.body.appendChild(link);
          link.click();
        } catch (error) {
          this.watingForDownload = false;
          console.error("Error downloading apk", error);
          // Handle error as needed
        }
      }
    },
