<template>
  <div>
    <div
      class="navbar bg-primary absolute top-0 text-primary-content z-[1]"
      v-if="!loading"
    >
      <router-link to="/" class="btn btn-ghost normal-case text-xl"
        >web to app</router-link
      >
    </div>

    <div
      class="fixed z-[0] inset-0 w-screen bg-base-100 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"
    ></div>

    <div
      v-if="loading"
      class="w-screen h-screen flex justify-center items-center flex-col gap-2"
    >
      <span class="loading loading-spinner loading-lg text-primary"></span>
      <p>building your app...</p>
    </div>
    <div v-if="!loading" class="min-h-screen w-screen">
      <div class="min-h-screen w-screen flex justify-center items-center">
        <div
          class="w-11/12 max-w-lg shadow-2xl rounded-box p-6 flex flex-col gap-3 z-10 bg-base-100"
        >
          <!-- <progress
            class="progress progress-primary w-full mb-3 h-3"
            :value="progressValue"
            max="100"
          ></progress> -->

          <div class="w-full form-control mb-2">
            <label class="label">
              <span class="label-text">website URL</span>
            </label>
            <input
              class="input input-bordered join-item w-full"
              v-model="url"
              type="url"
              placeholder="Website URL"
            />
            <label class="label">
              <span class="label-text">app name</span>
            </label>
            <input
              class="input input-bordered join-item w-full"
              v-model="appName"
              type="url"
              placeholder="app name"
            />
            <div class="flex flex-row items-center gap-2 my-2">
              <label for="package-name">package name</label>
              <div
                class="tooltip"
                data-tip="indentifier of your app. can't be changed after you publish (com.yourCompany.appName)"
              >
                <BootstrapIcon icon="question-circle" size="x" />
              </div>
            </div>
            <input
              class="input input-bordered join-item w-full"
              v-model="packageName"
              type="url"
              placeholder="package name (com.yourcomapny.appname)"
            />
          </div>
          <div class="w-full flex flex-row justify-between items-end mb-3">
            <div class="flex flex-col">
              <label for="logo">logo</label>
              <input
                type="file"
                id="logo"
                class="file-input file-input-primary file-input-bordered w-full max-w-xs"
                @change="handleLogoUpload"
              />
            </div>
            <div class="bg-neutral w-16 h-16 rounded-box">
              <img :src="previewImage" class="w-16 h-16 rounded-box" />
            </div>
          </div>
          <div class="flex items-center justify-start gap-2">
            <button
              class="btn bg-base-100 border-primary border-4"
              v-if="buildOS === 'android'"
            >
              <BootstrapIcon icon="android2" class="text-success" size="2x" />
            </button>
            <button @click="buildOS = 'android'" class="btn bg-base-100" v-else>
              <BootstrapIcon icon="android2" class="text-success" size="2x" />
            </button>
            <button
              class="btn bg-base-100 border-primary border-4"
              v-if="buildOS === 'ios'"
            >
              <BootstrapIcon icon="apple" size="2x" class="text-gray-500" />
            </button>
            <button @click="buildOS = 'ios'" class="btn bg-base-100" v-else>
              <BootstrapIcon icon="apple" size="2x" class="text-gray-500" />
            </button>
          </div>
          <div class="w-full flex justify-center text-error">
            <p>{{ errorMessage }}</p>
          </div>
          <button @click="submitData" class="btn btn-success w-full p-3">
            submit
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      previewImage: null,
      url: this.$route.params.url,
      appName: "app" + Date.now(),
      packageName: "com." + "app" + Date.now() + ".app" + Date.now(),
      progressValue: 50,
      buildOS: "android",
      loading: false,
      logo: null,
      errorMessage: "",
    };
  },
  methods: {
    handleLogoUpload(event) {
      const file = event.target.files[0];

      if (file) {
        this.logo = file;
        this.previewImage = URL.createObjectURL(file);
      }
    },

    isValidUrl(url) {
      const pattern = new RegExp(
        "^(https?:\\/\\/)" +
          // protocol
          "((([a-zA-Z\\d]([a-zA-Z\\d-]{0,61}[a-zA-Z\\d])?)\\.)+[a-zA-Z]{2,6}" +
          // domain name
          "|(\\d{1,3}\\.){3}\\d{1,3}" +
          // OR ip (v4) address
          "|localhost)" +
          // OR localhost
          "(\\:\\d+)?(\\/[-a-zA-Z\\d%_.~+]*)*" +
          // port and path
          "(\\?[;&a-zA-Z\\d%_.~+=-]*)?" +
          // query string
          "(\\#[-a-zA-Z\\d_]*)?$" // fragment locator
      );
      return !!pattern.test(url);
    },

    validateAppName(name) {
      // Add your own validation logic for the app name
      return name.length > 0;
    },

    validatePackageName(packageName) {
      // Add your own validation logic for the package name
      var pattern = /^[a-z][a-z0-9_]*(\.[a-z0-9_]+)+[0-9a-z_]$/i;

      return pattern.test(packageName);
    },

    async submitData() {
      if (!this.isValidUrl(this.url)) {
        console.error("Invalid URL");
        this.errorMessage = "Invalid URL";
        return;
      }

      if (!this.validateAppName(this.appName)) {
        console.error("Invalid app name");
        this.errorMessage = "Invalid app name";
        return;
      }

      if (!this.validatePackageName(this.packageName)) {
        console.error("Invalid package name");
        this.errorMessage = "Invalid package name";
        return;
      }

      if (this.previewImage == null) {
        console.error("upload a logo");
        this.errorMessage = "upload a logo";
        return;
      }

      try {
        this.loading = true;
        const formData = new FormData();
        formData.append("url", this.url);
        formData.append("name", this.appName);
        formData.append("packageName", this.packageName);
        formData.append("logo", this.logo, this.logo.name);

        if (this.buildOS == "android") {
          const response = await axios.post(
            "http://34.125.138.233/submit-url-android",
            formData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
            }
          );

          console.log("Response:", response.data);
          this.loading = false;

          this.$router.push({
            name: "downloadDemo",
            params: {
              token: response.data.token,
              os: "android",
            },
          });
        } else if (this.buildOS == "ios") {
          const response = await axios.post(
            "http://34.125.138.233/submit-url-ios",
            formData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
            }
          );

          console.log("Response:", response.data);
          this.loading = false;

          this.$router.push({
            name: "downloadDemo",
            params: {
              token: response.data.token,
              os: "ios",
            },
          });
        }
      } catch (error) {
        this.loading = false;
        console.error("An error occurred:", error);
      }
    },
  },
};
</script>
