<template>
  <div>
    <div
    v-if="!loadingRebuild"
      class="navbar bg-primary fixed top-0 w-full z-[20] text-primary-content"
    >
      <div class="flex-1">
        <router-link to="/" class="btn btn-ghost normal-case text-xl"
          >web to app</router-link
        >
      </div>
      <div class="flex-none">
        <button @click="rebuildDemo" v-if="status == 'demo'" class="btn btn-success">rebuild app</button>
        <button @click="rebuildBundle" v-if="status == 'final'" class="btn btn-success">rebuild bundle</button>
      </div>
    </div>
    <div
      class="w-screen h-screen absolute top-0 flex justify-center items-center bg-base-100 flex-col gap-2"
      v-if="loadingRebuild"
    >
      <span
        class="loading loading-spinner loading-lg text-primary opacity-100"
      ></span>
      <p>rebuilding your app...</p>
    </div>

    <div class="relative h-screen flex mt-16 overflow-hidden" v-if="!loadingRebuild">
      <div
        v-if="isDrawerOpen"
        class="h-screen w-72 shadow-lg p-4 bg-base-100 absolute z-20 md:relative md:z-10"
      >
        <ul class="space-y-2">
          <div class=" w-full flex justify-end">
            <button
            @click="toggleDrawer"
            class="btn btn-primary md:hidden"
          >
            x
          </button>
          </div>
          <li
            @click="tabIndex = 1"
            :class="{ ' bg-base-300': tabIndex === 1 }"
            class="cursor-pointer p-3 rounded-xl hover:bg-base-200"
          >
            app info
          </li>
          <li
            @click="tabIndex = 2"
            :class="{ 'bg-base-300': tabIndex === 2 }"
            class="cursor-pointer p-3 rounded-xl hover:bg-base-200"
          >
            more
          </li>
        </ul>
      </div>

      <!-- Your main content here -->
      <div class="mx-auto flex-1 mt-5">
        <div class="w-full flex">
          <button
            @click="toggleDrawer"
            v-if="!isDrawerOpen"
            class="btn btn-primary md:hidden m-5"
          >
            open menu
          </button>
        </div>
        <!-- Form 1 -->
        <div v-if="tabIndex === 1">
          <div class="m-4 flex flex-col gap-4">
            <div class="flex flex-col">
              <label for="name" class="mb-3">App name</label>
              <input
                id="name"
                type="text"
                placeholder="Type here"
                class="input input-bordered w-full max-w-5xl"
                v-model="appName"
              />
            </div>
            <div class="flex flex-col">
              <div class="flex flex-row items-center gap-2">
                <label for="package-name" class="mb-3">package name</label>
                <div
                  class="tooltip"
                  data-tip="indentifier of your app. can't be changed after you publish (com.yourCompany.appName)"
                >
                  <BootstrapIcon icon="question-circle" size="x" />
                </div>
              </div>

              <input
                id="package-name"
                type="text"
                placeholder="Type here"
                class="input input-bordered w-full max-w-5xl"
                v-model="packageName"
              />
            </div>

            <div class="flex flex-col">
              <div class="flex flex-row items-center gap-2">
                <label for="version-code" class="mb-3">version code</label>
                <div
                  class="tooltip"
                  data-tip="version of your app, if you want to update your app, increase the version number"
                >
                  <BootstrapIcon icon="question-circle" size="x" />
                </div>
              </div>

              <input
                id="versio-code"
                v-model="versionCode"
                type="number"
                placeholder="Type here"
                class="input input-bordered w-full max-w-xl"
              />
            </div>

            <div class="max-w-xl flex flex-row justify-between items-end">
              <div class="flex flex-col">
                <label for="logo" class="mb-3">logo</label>
                <input
                  type="file"
                  id="logo"
                  class="file-input file-input-primary file-input-bordered w-full max-w-xs"
                  @change="handleLogoUpload"
                />
              </div>
              <div class="bg-neutral w-16 h-16 rounded-box">
                <img :src="previewImage" class="w-16 h-16 rounded-box" />
              </div>
            </div>
          </div>
          <div class="ml-4 w-full flex justify-start text-error">
            <p>{{ errorMesssage }}</p>
          </div>
        </div>
        <!-- Form 2 -->
        <div v-if="tabIndex === 2">
          <form>
            <!-- Form 2 elements here -->

            <input
              type="text"
              placeholder="Form 2 Input"
              class="border p-2 mb-2"
            />
          </form>
        </div>
      </div>
    </div>
  </div>
  <div
    class="w-screen h-screen absolute top-0 flex justify-center items-center z-30 bg-black bg-opacity-40"
    v-if="loading"
  >
    <span
      class="loading loading-spinner loading-lg text-primary opacity-100"
    ></span>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      loadingRebuild: false,
      loading: true,
      isDrawerOpen: true,
      tabIndex: 1,
      previewImage: null,
      appName: null,
      packageName: null,
      logo: null,
      errorMesssage: "",
      versionCode: null,
      status: "",
    };
  },

  async created() {
    const token = this.$route.params.token;
    const status = this.$route.params.status;
    this.status = status;
    if (status === "demo") {
      try {
        const response = await axios.get(
          `http://34.125.138.233/get-data-android/${token}`
        );
        console.log(response.data);
        this.appName = response.data.name;
        this.packageName = response.data.packageName;
        this.versionCode = response.data.versionCode;

        this.previewImage = "data:image/png;base64," + response.data.logo;
        this.loading = false;
      } catch (error) {
        console.error("Error fetching data:", error);
        if (error.response.status === 404) {
          this.loading = false;
          this.$router.push({ name: "notFound" });
        }
      }
    }

    else if (status === "final") {
      try {
      const response = await axios.get(
        `http://34.125.138.233/get-final-data-android/${token}`
      );
      console.log(response.data);
      this.appName = response.data.name;
      this.packageName = response.data.packageName;
      this.versionCode = response.data.versionCode;

      this.previewImage = "data:image/png;base64," + response.data.logo;
      this.loading = false;
    } catch (error) {
      console.error("Error fetching data:", error);
      if (error.response.status === 404) {
        this.loading = false;
        this.$router.push({ name: "notFound" });
      }
    }
    }
  },
  methods: {
    handleLogoUpload(event) {
      const file = event.target.files[0];

      if (file) {
        this.logo = file;
        this.previewImage = URL.createObjectURL(file);
      }
    },
    toggleDrawer() {
      this.isDrawerOpen = !this.isDrawerOpen;
    },

    validateAppName(name) {
      // Add your own validation logic for the app name
      return name && name.length > 0;
    },

    validatePackageName(packageName) {
      // Add your own validation logic for the package name
      var pattern = /^[a-z][a-z0-9_]*(\.[a-z0-9_]+)+[0-9a-z_]$/i;

      return pattern.test(packageName);
    },
    validateVersionCode(versionCode) {
      // Add your own validation logic for the package name
      return versionCode;
    },

    async rebuildDemo() {
      this.loadingRebuild = true;
      if (this.loading) {
        return;
      }
      if (!this.validateAppName(this.appName)) {
        console.error("Invalid app name");
        this.errorMesssage = "Invalid app name";
        return;
      }

      if (!this.validatePackageName(this.packageName)) {
        console.error("Invalid package name");
        this.errorMesssage = "Invalid package name";
        return;
      }

      if (!this.validateVersionCode(this.versionCode)) {
        console.error("Invalid version code");
        this.errorMesssage = "Invalid version code";
        return;
      }

      this.loadingRebuild = true;
      const formData = new FormData();
      formData.append("token", this.$route.params.token);
      formData.append("name", this.appName);
      formData.append("packageName", this.packageName);
      formData.append("versionCode", this.versionCode);

      if (this.previewImage == null) {
        this.errorMesssage = "upload a logo";
      } else {
        if (this.logo) {
          formData.append("logo", this.logo);
        } else {
          // If no new logo is uploaded, send the existing logo data
          const existingLogoData = this.previewImage.replace(
            /^data:image\/[a-z]+;base64,/,
            ""
          );
          formData.append("logo", existingLogoData);
        }
      }

      try {
        const response = await axios.post(
          "http://34.125.138.233/rebuild-demo-android",
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        );

        // Handle the response data as needed
        console.log("Rebuild response:", response.data);
        // this.loadingRebuild = false;
        this.$router.push({
          name: "downloadDemo",
          params: {
            token: response.data.token,
            os: "android",
          },
        });
      } catch (error) {
        // Handle errors here
        console.error("Error during rebuild:", error);
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
        this.loadingRebuild = false;
      }
    },


    async rebuildBundle() {
      this.loadingRebuild = true;
      if (this.loading) {
        return;
      }
      if (!this.validateAppName(this.appName)) {
        console.error("Invalid app name");
        this.errorMesssage = "Invalid app name";
        return;
      }

      if (!this.validatePackageName(this.packageName)) {
        console.error("Invalid package name");
        this.errorMesssage = "Invalid package name";
        return;
      }

      if (!this.validateVersionCode(this.versionCode)) {
        console.error("Invalid version code");
        this.errorMesssage = "Invalid version code";
        return;
      }

      
      const formData = new FormData();
      formData.append("token", this.$route.params.token);
      formData.append("name", this.appName);
      formData.append("packageName", this.packageName);
      formData.append("versionCode", this.versionCode);

      if (this.previewImage == null) {
        this.errorMesssage = "upload a logo";
      } else {
        if (this.logo) {
          formData.append("logo", this.logo);
        } else {
          // If no new logo is uploaded, send the existing logo data
          const existingLogoData = this.previewImage.replace(
            /^data:image\/[a-z]+;base64,/,
            ""
          );
          formData.append("logo", existingLogoData);
        }
      }

      try {
        const response = await axios.post(
          "http://34.125.138.233/rebuild-bundle-android",
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        );

        // Handle the response data as needed
        console.log("Rebuild response:", response.data);
        // this.loadingRebuild = false;
        this.$router.push({
          name: "profilePage",
          params: {
            email: this.$route.email
          },
        });
      } catch (error) {
        // Handle errors here
        console.error("Error during rebuild:", error);
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
        this.loadingRebuild = false;
      }
    },
  },
};
</script>

