<template>
  <div class="navbar bg-primary absolute top-0 text-primary-content">
    <router-link to="/" class="btn btn-ghost normal-case text-xl"
      >web to app</router-link
    >
  </div>

  <div
    class="fixed z-[0] inset-0 w-screen bg-base-100 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"
  ></div>

  <div class="w-screen h-screen">
    <div class="w-full h-full flex justify-center items-center">
      <div
        class="card card-side bg-base-100 shadow-2xl flex flex-col sm:flex-row mt-10"
      >
        <div class="card-body">
          <h2 class="card-title">
            <BootstrapIcon icon="android2" class="text-success" size="2x" />
            android demo
          </h2>
          <p>click the button to download the app apk</p>

          <div class="card-actions">
            <button class="btn btn-success z-10 w-full" @click="downloadApk">
              <span
                v-if="watingForDownload"
                class="loading loading-spinner loading-lg opacity-100"
              ></span>
              <span v-else> Download APK </span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: this.$route.params.token, // Replace with your actual token
      appName: "",
      websiteUrl: "",
      logoSrc: "",
      packageName: "",
      watingForDownload: false,
    };
  },

  methods: {
    async downloadApk() {
      this.watingForDownload = true;
      try {
        const response = await axios.get(
          `http://34.125.138.233/download-demo-apk-android?token=${this.token}.0`,
          {
            responseType: "blob", // Set the response type to 'blob'
          }
        );
        this.watingForDownload = false;
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "apk-debu.apk"); // Set the desired file name
        document.body.appendChild(link);
        link.click();
      } catch (error) {
        this.watingForDownload = false;
        console.error("Error downloading apk", error);
        // Handle error as needed
      }
    },
  },
};
</script>

<style>
* {
  z-index: 10;
}
</style>
